<template>
  <AlertMessage v-if="messageDisplay"/>
 <div class="hero_area">
<SliderSection/>
</div>

<WhySection class="why-section" />
<ArrivalSection/>
<ProductSection class="product-section" />
<SubscribeSection class="subdcibe-section"/>
<ClientSection/>
<Contact class="contact-section"/>
</template>

<script>
import { onMounted, onUnmounted, inject, ref } from 'vue';
import ArrivalSection from './ArrivalSection.vue';
import ClientSection from './ClientSection.vue';
import ProductSection from './ProductSection .vue';
import SliderSection from './SliderSection.vue';
import SubscribeSection from './SubscribeSection.vue';
import WhySection from './WhySection.vue';
import Contact from './Contact.vue';
import AlertMessage from './AlertMessage.vue';

export default {
  name: 'HelloWorld',
  components:{
    AlertMessage,
    ClientSection,
    SubscribeSection,
    ProductSection,
    ArrivalSection,
    WhySection,
    SliderSection,
    Contact,
  },
  setup() {
    const eventBus = inject('eventBus'); 
    const messageDisplay = ref(false);

    onMounted(()=>{
      eventBus.on('ShowMessageDisplay',(checked)=>{
        messageDisplay.value = checked;
      })
    })
    onUnmounted(()=>{
      eventBus.off('ShowMessageDisplay');
    })

    // 使用 querySelector 根據 class 滾動到特定元素
    const scrollToComponent = (selector) => {
      const element = document.querySelector(selector); // 根據 class 查找 DOM 元素
      if (element) {
        element.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
        });
      } else {
        console.warn(`Element with selector '${selector}' not found`);
      }
    };

    onMounted(() => {
      if (eventBus) {
        // 當接收到事件時，scrollToComponent 會被調用，並且 selector 會被傳入
        eventBus.on('CallHomeToScrollSection', scrollToComponent);
      }
    });

    onUnmounted(() => {
      if (eventBus) {
        // 卸載事件監聽
        eventBus.off('CallHomeToScrollSection', scrollToComponent);
      }
    });

    return {
      scrollToComponent,
      messageDisplay
    };
  }

}
</script>

