<template>
  <AlertMessage v-if="checkMessage"/>

  <div class="shoppingcart">
    <div class="AllDiv">
      
      <ShoppingCart_CheckoutAmount
      :ProductDetails="ProductDetails" 
      :thisPproductAllPrice="thisPproductAllPrice"/>

      <form action="">
        <div class="marginbotton20">
          <div class="circleHead">
            <span>1</span>
            <label>購物車內容</label>
          </div>
          <div class="WhiteBackground">
            <table >
              <thead>
                <tr>
                  <th>商品詳細</th>
                  <th class="centerThis OnePrice">單價</th>
                  <th class="centerThis">數量</th>
                  <th class="centerThis">小計</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(ProductDetail, index) in ProductDetails" :key="index">
                  <td>
                    <div class="productName">
                      <img :src="ProductDetail.thisProductImageUrl">
                      <div>{{ ProductDetail.thisProductName }}</div>
                    </div>                    
                  </td>
                  <td class="centerThis width88 OnePrice">NT${{ ProductDetail.thisProductOnePrice }}</td>
                  <td class="centerThis width170">
                    <div class="quantityInfo">
                      <button type="button" @click="ProductQuantityMinus(ProductDetail)">-</button>
                      <input type="text" :placeholder="ProductDetail.thisProductAllQuantity">
                      <button type="button" @click="ProductQuantityAdd(ProductDetail)">+</button>
                    </div>
                  </td>
                  <td class="centerThis width88">NT${{ thisPproductAllPrice(ProductDetail) }}</td>
                  <td class="centerThis">
                    <svg @click="deleteProduct(ProductDetail)" width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path fill-rule="evenodd" clip-rule="evenodd" d="M15.7071 1.70711C16.0976 1.31658 16.0976 0.683418 15.7071 0.292893C15.3166 -0.0976309 14.6834 -0.0976309 14.2929 0.292893L8 6.58579L1.70711 0.292893C1.31658 -0.0976309 0.683418 -0.0976309 0.292893 0.292893C-0.0976309 0.683418 -0.0976309 1.31658 0.292893 1.70711L6.58579 8L0.292893 14.2929C-0.0976309 14.6834 -0.0976309 15.3166 0.292893 15.7071C0.683418 16.0976 1.31658 16.0976 1.70711 15.7071L8 9.41421L14.2929 15.7071C14.6834 16.0976 15.3166 16.0976 15.7071 15.7071C16.0976 15.3166 16.0976 14.6834 15.7071 14.2929L9.41421 8L15.7071 1.70711Z" fill="#565656"></path>
                    </svg>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="6">
                    <div>
                      <span>購物車內合計有 {{ totalQuantity }} 項商品</span>
                    </div>
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>       
        </div>

        <ShoppingCart_MemberLogin/>
        <ShoppingCart_PaymentShippingMethods/>
        
        <div>
          <button type="button" class="submitButton border6px" @click="submit">立即結帳</button>
        </div>
      </form>
    </div>    
  </div>  
</template>

<script>
import router from '@/router/router';
import AlertMessage from './AlertMessage.vue';
import ShoppingCart_MemberLogin from './ShoppingCart_ MemberLogin.vue';
import ShoppingCart_CheckoutAmount from './ShoppingCart_CheckoutAmount.vue';
import ShoppingCart_PaymentShippingMethods from './ShoppingCart_Payment&ShippingMethods.vue';
import { computed, inject,  nextTick,  onMounted, onUnmounted, reactive, ref } from 'vue';

export default {
  components:{
    ShoppingCart_CheckoutAmount,
    ShoppingCart_MemberLogin,
    ShoppingCart_PaymentShippingMethods,
    AlertMessage
  },
  setup(){    
    onMounted(() =>{
      loadFromStorage();
      window.scroll(0,0);
      //如果購物車為0，自動導航回首頁
      if(ProductDetails.length === 0){
        router.push('/');
      }
    })
    const eventBus = inject('eventBus');
    const ProductDetails = reactive([]);

    const loadFromStorage = () =>{
      const storedProduct = JSON.parse(localStorage.getItem('ProductDetails'));
      if(storedProduct){
        ProductDetails.splice(0, ProductDetails.length, ...storedProduct);
      }
    }    

    //商品詳細
    const saveThisProductToStorage = () =>{
      localStorage.setItem('ProductDetails',JSON.stringify(ProductDetails))
    }

    const ProductQuantityMinus = (ProductDetail) =>{
      if(ProductDetail.thisProductAllQuantity>1){
        ProductDetail.thisProductAllQuantity -=1;
      }
      saveThisProductToStorage();
      eventBus.emit('updateProductDetails', ProductDetails);
    };
    const ProductQuantityAdd = (ProductDetail) =>{
      ProductDetail.thisProductAllQuantity +=1;
      saveThisProductToStorage();
      eventBus.emit('updateProductDetails', ProductDetails);
    };

    const thisPproductAllPrice = (ProductDetail) =>{
      return (ProductDetail.thisProductAllQuantity) * (ProductDetail.thisProductOnePrice);
    };

    const totalQuantity = computed(() =>{
      const productTotalTotal = ProductDetails.reduce((total,product) =>{
        return total + product.thisProductAllQuantity;
      },0);
      return productTotalTotal;
    })

    const deleteProduct = (ProductDetail) => {
      const index = ProductDetails.indexOf(ProductDetail);
      if(index > -1){
        ProductDetails.splice(index,1);
        saveThisProductToStorage();
        eventBus.emit('updateProductDetails', ProductDetails);
      }
      if(ProductDetails.length === 0){
        checkMessage.value = true;
        nextTick(()=>{
          eventBus.emit('sentCheckMessage','購物車為空，將回到首頁');
        })
        setTimeout(()=>{
          checkMessage.value = false;
          router.push('/');
        },2000)
      }
    };

    //確認條文有沒有都勾選了
    const checkMessage = ref(false);
    const isPrivacyChecked = reactive({value:false});
    const isShippingChecked  = reactive({value:false});
    const isMarketingChecked  = reactive({value:false});
    //確認購買人資訊
    const isCustomerNameChecked = reactive({value:false});
    const isCustomerPhone = reactive({value:false});
    const isCustomerMail = reactive({value:false});
    const isCustomeaddress = reactive({value:false});
    //確認收件人資訊
    const selectedReceiver = ref('me');
    const isReceiverNameChecked = reactive({value:false});
    const isReceiverPhoneChecked = reactive({value:false});
    const isReceiverAddressChecked = reactive({value:false});
    //確認發票資訊
    const isBillChecked = reactive({value:true});
    onMounted(() =>{
      eventBus.on('emitPrivacyChecked', (checked)=>{
        isPrivacyChecked.value = checked;
      })
      eventBus.on('emitShippingChecked', (checked)=>{
        isShippingChecked.value = checked;
      })
      eventBus.on('emitMarketingChecked', (checked)=>{
        isMarketingChecked.value = checked;
      })
      eventBus.on('sentBackCustomerName', (checked)=>{
        isCustomerNameChecked.value = checked;
      })
      eventBus.on('sentBackCustomerPhone', (checked)=>{
        isCustomerPhone.value = checked;
      })
      eventBus.on('sentBackCustomerMail', (checked)=>{
        isCustomerMail.value = checked;
      })
      eventBus.on('sentBackCustomeraddress', (checked)=>{
        isCustomeaddress.value = checked;
      })
      eventBus.on('sentBackReceiverName', (checked)=>{
        isReceiverNameChecked.value = checked;
      })
      eventBus.on('sentBackReceiverPhone', (checked)=>{
        isReceiverPhoneChecked.value = checked;
      })
      eventBus.on('sentBackReceiverAddres', (checked)=>{
        isReceiverAddressChecked.value = checked;
      })
      eventBus.on('selectedReceiverChanged', (reactive)=>{
        selectedReceiver.value = reactive
      })
      eventBus.on('sentBackAskBill', (checked)=>{
        isBillChecked.value = checked;
      })
    })
    onUnmounted(()=>{
       eventBus.off('emitPrivacyChecked');
       eventBus.off('emitShippingChecked');
       eventBus.off('emitMarketingChecked');
       eventBus.off('sentBackCustomerName');
       eventBus.off('sentBackCustomerPhone');
       eventBus.off('sentBackCustomerMail');
       eventBus.off('sentBackCustomeraddress');
       eventBus.off('sentBackReceiverName');
       eventBus.off('sentBackReceiverPhone');
       eventBus.off('sentBackReceiverAddres');
       eventBus.off('selectedReceiverChanged', selectedReceiver);
       eventBus.off('sentBackAskBill');
    })
    const submit = async() =>{
        eventBus.emit('sentToAskCustomerName');
        eventBus.emit('sentToAskCustomerPhone');
        eventBus.emit('sentToAskCustomerMail');
        eventBus.emit('sentToAskCustomerAddress');
        eventBus.emit('sentToAskselectReceiver');
        eventBus.emit('sentToAskBill');

      //勾選警告顯示
      if(!isPrivacyChecked.value){
        eventBus.emit('showPrivacyWarning');
        checkMessage.value = true;        
        const message = '請勾選同意個資聲明以繼續結帳';
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      if(!isShippingChecked.value){
        eventBus.emit('showShippingWarning');
        checkMessage.value = true;
        const message = '請同意出貨條款';
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }

      //購買人input空白警告
      if(!isCustomerNameChecked.value){
        checkMessage.value = true;
        const message = '請填寫購買人姓名';
        eventBus.emit('ScrollWindow', '.CustomerName')
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      if(!isCustomerPhone.value){
        checkMessage.value = true;
        const message = '請填寫購買人連絡電話';
        eventBus.emit('ScrollWindow', '.CustomerPhone')
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      if(!isCustomerMail.value){
        checkMessage.value = true;
        const message = '請填寫購買人連絡信箱';
        eventBus.emit('ScrollWindow', '.CustomerMail')
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      if(!isCustomeaddress.value){
        checkMessage.value = true;
        const message = '請填寫購買人地址';
        eventBus.emit('ScrollWindow', '.Customeraddress')
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      //收件人input空白警告
      if(selectedReceiver.value === 'other'){
        await nextTick();
        if(!isReceiverNameChecked.value){
          checkMessage.value = true;
          const message = '請填寫收件人姓名';
          eventBus.emit('ScrollWindow', '.ReceiverName')
          nextTick(()=>{
            eventBus.emit('sentCheckMessage', message);
          })
          setTimeout(() =>{
            checkMessage.value = false;
          },2000);
          return;
        }
        if(!isReceiverPhoneChecked.value){
          checkMessage.value = true;
          const message = '請填寫收件人電話';
          eventBus.emit('ScrollWindow', '.ReceivePhone')
          nextTick(()=>{
            eventBus.emit('sentCheckMessage', message);
          })
          setTimeout(() =>{
            checkMessage.value = false;
          },2000);
          return;
        }
        if(!isReceiverAddressChecked.value){
          checkMessage.value = true;
          const message = '請填寫收件人地址';
          eventBus.emit('ScrollWindow', '.ReceiveAddress')
          nextTick(()=>{
            eventBus.emit('sentCheckMessage', message);
          })
          setTimeout(() =>{
            checkMessage.value = false;
          },2000);
          return;
        }
      }
      //發票警告
      if(!isBillChecked.value){
        checkMessage.value = true;
        const message = '請填寫正確發票格式';
        eventBus.emit('ScrollWindow', '.CustomerBill')
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', message);
        })
        setTimeout(() =>{
          checkMessage.value = false;
        },2000);
        return;
      }
      alert('模擬操作到此，謝謝你的使用')
      // console.log("Form submitted");
    };

    return {
      ProductDetails,
      ProductQuantityMinus,
      ProductQuantityAdd,
      thisPproductAllPrice,
      totalQuantity,
      deleteProduct,
      submit,
      checkMessage,
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep *{
  margin: 0;
}
::v-deep hr{
  margin: 20px 0;
}
$thisRed: #f7444e;
::v-deep .pointRed{
  display: none;
  color: #dc3545;
  font-size: 13px;
  margin-top: 4px;
}
::v-deep input.error {
  background-color: #ffeeee;
  border-color: #dc3545;
}
::v-deep .pointGreen{
  color: #0c5460;
  font-size: 12px;
  margin-top: 4px;
}
::v-deep .pointPink{
  color: var(--checkout_accent, #ff6855);
  font-size: 12px;
  margin-top: 4px;
}

.shoppingcart{
  background-color: #f9f9f9;
  display: flex;
  justify-content: center;

  .AllDiv{
    display: flex;
    justify-content: space-between;
    gap: 16px;
    padding: 20px;  
  }
}
input{
  margin: 0;
  padding: 0 16px;
  height: 46px;
}

::v-deep .border6px{
  border-radius: 6px;
}
::v-deep form{
  .marginBottom12px{
    margin-bottom: 12px;
  }
  .paddingUpDown16{
    padding: 16px 0;
    box-sizing: border-box;
  }
  .paddingLeftRight16{
    padding: 0 16px;
    box-sizing: border-box;
  }
  .marginbotton20{
    margin-bottom: 20px;
    width: 100%;
  }
  .checkCircle{
    border: 1px solid #e2e2e2;
    border-radius: 50px;
    height: 25px;
    width: 25px;
    margin-right: 10px;
  }
  .checkCircleIt{
    border: 1px solid $thisRed;
    background-color: $thisRed;
    border-radius: 50px;
    height: 25px;
    width: 25px;
    margin-right: 10px;
  }
  .WhiteBackground {
    background-color: #ffffff !important;
    border-radius: 4px;
    border: solid 1px #b1b1b1;
    border-collapse: separate;
    width: 664px;

    label{
      margin-bottom: 5px;
    }
    .nameLabel{
      margin-bottom: 16px;
    }
    .ButtonDiv{
      display: flex;
      justify-content: space-between;
      gap: 8px;
      margin-bottom: 12px;

      .GUIInput{
        width: 100%;
      }            
      .chooseButton{
        display: flex;
        width: 50%;
        background-color: #fff;
        border: 1px solid #e2e2e2;
        border-radius: 5px;
        padding: 16px 24px;
        cursor: pointer;
      }
      .chooseButtonCheck{
        display: flex;
        width: 50%;
        background-color: #fff;
        border: 2px solid var(--checkout_button, #ff6855);
        border-radius: 5px;
        padding: 16px 24px;
        cursor: pointer;
      }
    }
    .cardinputDiv{
      display: flex;

      .cardType{
        height: 46px;
        width: 53px;
        background-color: #e9ecef;
        padding: 12px;
        box-sizing: border-box;

        img{
          opacity: 0.5;
        }
      }
    }
    .addressDiv{
      display: flex;
      gap: 16px;

      .addressInput{
        width: 33%;
      }
    }
    .billDiv{
      display: flex;
      justify-content: start;
      gap: 16px;

      .billButton{
        display: flex;
        cursor: pointer;
      }
    }
    .AllBillLow{
      .billLow{
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #f0f1f3;
        padding: 10px 16px;
        font-size: 13px;
        margin-top: 20px;
        cursor: pointer;
      }
      .billLowdown{
        background-color: #f0f1f3;
        padding: 10px 16px;
        font-size: 13px;
        max-width: 725px;
      }
      .slide-fade-enter-active,
      .slide-fade-leave-active {
        transition: all 0.3s ease;
      }

      .slide-fade-enter-from,
      .slide-fade-leave-to {
        height: 0;
        opacity: 0;
      }

      .slide-fade-enter-to,
      .slide-fade-leave-from {
        height: auto;
        opacity: 1;
      }
    }    
    .myselfDiv{
      display: flex;
      flex-direction: column;
      gap: 12px;
      background-color: #f9f9f9;
      padding: 20px;
      box-sizing: border-box;
      
      p{
        font-size: 13px;
        color: #565656;
      }
    }
    .InformationCheckDiv{
      display: flex;

      .checkbox{
        height: 13px;
        width: 13px;
        margin-right: 4px;
      }
      p{
        font-size: 13px;
        color: #565656;
      }
    }
  }
  .circleHead{
    display: flex;
    height: auto;
    font-size: 17px;
    margin-bottom: 15px;

    span{
      width: 24px;
      height: 24px;
      border-radius: 50px;
      background-color: var(--default_dark, #a3a3a3);
      color: #fff;
      font-size: 14px;
      text-align: center;
      line-height: 24px;
    }
    label{
      padding-left: 10px;
    }
  }
  table{
    width: 100%;

    thead{
      th{
        background-color:  #e9e9ec;
        font-size: 14px;
        font-weight: normal;
        padding: 10px;
      }
      .centerThis{
        text-align: center;
      }
    }
    tbody{
      .width170{
          width: 170px;
        }
      .width88{
        width: 88px;
      }
      tr{
        border-bottom: solid 1px #b1b1b1;

        td{
        padding: 18px 0;

        .productName{
          display: flex;
          align-items: center;
          box-sizing: border-box;
          padding: 0 15px;

          img{
            height: 80px;
            width: 80px;
            object-fit: cover;
          }
        }        
      }
      .centerThis{
        text-align: center;
        padding: 18px 15px;

        svg{
          cursor: pointer;
        }
      }
      .quantityInfo{
        display: flex;
        justify-content: center;

        input{
          width: 32px;
          height: 32px;
          padding: 0;
          text-align: center;
          margin: 0 8px;
        }
        button {
          width: 32px;
          height: 32px;
          border: solid 1px #b1b1b1;
          background-color: #fff;
        }
      }
      }
      
    }
    tfoot{
      td{
        text-align: right;
        padding: 16px;
        font-size: 12px;
      }
    }
  }
  .submitButton{
    width: 100%;
    padding: 16px 20px;
    background-color: $thisRed;
    color: #fff;
    border: none;
  }
}
::v-deep .CashDivWhiteBackground {
  position: sticky;
  top: 20px;
  width: 256px;
  background-color: #ffffff !important;
  border-radius: 4px;
  border: solid 1px #b1b1b1;
  border-collapse: separate;
  padding: 16px 0;

  ::v-deep  hr{
    margin: 16px 0px;
  }  
}

@media (max-width:991px) {
  .AllDiv{
    display: flex;
    flex-direction: column;
    justify-content: none;
  }
  ::v-deep  .CashDivWhiteBackground {
    position: none;
    width: auto;
  }
  ::v-deep form{
    .WhiteBackground {
      width: auto;
    }
  }
}
@media (max-width:850px) {
  ::v-deep .thisbillDiv{
    display: flex;
    flex-direction: column;
  }
}
@media (max-width:600px) {
  ::v-deep .productName{
    img{
      display: none;
    }
  }
  .OnePrice{
      display: none;
    }
}


</style>