<template>
    <section class="slider_section ">
    <div class="slider_bg_box">
        <img class="bannerImage" src="images/slider-bg.jpg" alt="">
        <div class="grayBackground"></div>
    </div>
    <div id="customCarousel1" class="carousel slide" data-ride="carousel">
        <div class="carousel-inner">
          <div class="carousel-item active"
          v-for="(banner, index) in bannersSlice" :key="index">
              <div class="container ">
                <div class="row">
                    <div class="col-md-7 col-lg-6 ">
                      <div class="detail-box">
                          <h1>
                            <span class="oneTitle">
                            {{ banner.spanOne }}
                            </span>
                            <br>
                            {{ banner.spanTwo }}
                          </h1>
                          <p>
                            {{ banner.say }}
                          </p>
                          <div class="btn-box">
                            <a class="btn1" @click="scrollToProductSecion">
                              立即購買
                            </a>
                          </div>
                      </div>
                    </div>
                </div>
              </div>
          </div>

          <div class="carousel-item "
          v-for="(banner, index) in bannersSliceForOther" :key="index">
              <div class="container ">
                <div class="row">
                    <div class="col-md-7 col-lg-6 ">
                      <div class="detail-box">
                          <h1>
                            <span class="oneTitle">
                              {{ banner.spanOne }}
                            </span>
                            <br>
                            {{ banner.spanTwo }}
                          </h1>
                          <p>
                            {{ banner.say }}
                          </p>
                          <div class="btn-box">
                            <a href="" class="btn1">
                            立即購買
                            </a>
                          </div>
                      </div>
                    </div>
                </div>
              </div>
          </div>
        </div>
        <div class="container">
          <ol class="carousel-indicators">
              <li data-target="#customCarousel1" data-slide-to="0" class="active"></li>
              <li data-target="#customCarousel1" data-slide-to="1"></li>
              <li data-target="#customCarousel1" data-slide-to="2"></li>
          </ol>
        </div>
    </div>
  </section>
</template>

<script>
import { computed, inject, reactive } from 'vue';

export default {
  setup() {
    const eventBus = inject('eventBus');

    const scrollToProductSecion = () => {
      eventBus.emit('CallHomeToScrollSection', '.product-section')
    };

    const banners = reactive([
      {
        spanOne: '發現您的風格',
        spanTwo: 'On Fashion',
        say: '探索我們的最新系列，無論是簡約經典或是潮流前衛，我們的服裝設計讓你自信展現自我。立即開始你的穿搭之旅，創造屬於你的時尚宣言！'
      },
      {
        spanOne: '從日常到個性',
        spanTwo: 'On Everything',
        say: '我們的服飾不只是穿搭，更是對生活的詮釋。在這裡，每一件都是精挑細選，為你打造獨一無二的穿搭體驗。從經典到潮流，總有一款適合你。開始探索，找到屬於你的風格。'
      },
      {
        spanOne: '打造獨有穿搭',
        spanTwo: 'For You',
        say: '探索我們的最新服飾系列，從休閒風到正式場合應有盡有。我們的設計不僅追求品質，更注重每一位顧客的個性。快來發現屬於你的時尚，讓每一天都充滿自信與魅力！'
      },
    ]);

    const bannersSlice = computed(()=>{
      return banners.slice(0,1);
    });
    const bannersSliceForOther = computed(()=>{
      return banners.slice(1);
    })

    return {
      scrollToProductSecion,
      banners,
      bannersSlice,
      bannersSliceForOther
    }
  }

}
</script>

<style scoped>
a{
  cursor: pointer;
}
.slider_bg_box{
  display: flex;
}
.bannerImage{
  position: absolute;
  opacity: 0.7;
}
.grayBackground{
  content: '';
  height: 100%;
  width: 100%;
  background-color: rgb(12, 12, 12);
  opacity: 0.1;
  display: none;
}
.oneTitle{
  font-family: Montserrat, sans-serif;
}
p{
  font-family: Montserrat, sans-serif;
}

@media (max-width:767px) {
  .bannerImage{
    display: none;
  }
  .grayBackground{
    display: inline-block;
  }
}

</style>