<template>
  <section class="why_section layout_padding">
    <div class="container">
      <div class="heading_container heading_center">
        <h2>
          我要留言
        </h2>
    </div>
    <br><br>
    
      <div class="row">
          <div class="col-lg-8 offset-lg-2">
            <div class="full">
                <form @submit.prevent="submit">
                  <fieldset>
                      <input 
                      ref="nameInput" 
                      type="text" 
                      placeholder="輸入您的全名" 
                      name="name" 
                      required/>
                      <input 
                      ref="emailInput"
                      type="email" 
                      placeholder="輸入您的電子郵件" 
                      name="email" 
                      required />
                      <input 
                      ref="subjectInput"
                      type="text" 
                      placeholder="輸入您的標題" 
                      name="subject" 
                      required />
                      <textarea 
                      ref="messageInput"
                      placeholder="輸入您的留言" 
                      required></textarea>

                      <input
                      type="submit" 
                      value="送出"/>
                  </fieldset>
                </form>
            </div>
          </div>
      </div>
    </div>
</section>  
</template>

<script>
import { inject, nextTick, ref } from 'vue';



export default {
  setup(){
    const nameInput = ref(null);
    const emailInput = ref(null);
    const subjectInput = ref(null);
    const messageInput = ref(null);

    const eventBus = inject('eventBus');

    const submit = ()=>{
      nextTick(()=>{
        //顯示輸入成功並清空input
        eventBus.emit('ShowMessageDisplay',true);
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', '感謝您的意見！您的回饋是我們進步的動力。希望未來能為您帶來更好的體驗！');
          eventBus.emit('ShowMessageButton')
        })        
        console.log(
          `顧客姓名: ${nameInput.value.value}`,
          `顧客信箱: ${emailInput.value.value}`,
          `主題: ${subjectInput.value.value}`,
          `留言: ${messageInput.value.value}`
        );
        nameInput.value.value = '';
        emailInput.value.value = '';
        subjectInput.value.value = '';
        messageInput.value.value = '';
      })
    }

    return{
      nameInput,
      emailInput,
      subjectInput,
      messageInput,
      submit
    }
  }
}
</script>

<style scoped>
h2{
  font-family: Montserrat, sans-serif;
}

</style>