<template>
  <div>
    <div class="CashDivWhiteBackground">
      <div class="head">
        <label>結帳金額</label>
        <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 3L6 9L11 3" stroke="#8E99AD" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path></svg>
      </div>
      <hr>
      <div class="body">
        <span>商品金額</span>
        <span>NT${{ amountPayable }}</span>
      </div>      
      <hr>
      <div class="body">
        <span>運費</span>
        <span>+NT${{ deliveryCharge }}</span>
      </div>
      <hr>
      <div class="foot">
        <span>應付總額</span>
        <div>
          <span class="NT">NT$</span>
          <span class="dollar">{{ totalAmountPayable }}</span>
        </div>        
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue';
export default {
  props: {
    ProductDetails: {
      type: Array,
      required: true,
    },
    thisPproductAllPrice: {
      type: Function,
      required: true,
    },
  },
  setup(props){
    const deliveryCharge = computed(() =>{
      return amountPayable.value >= 230 ? 0 : 60;
    });

    const amountPayable = computed(() => {
      const productTotal = props.ProductDetails.reduce((total, product) => {
        return total + props.thisPproductAllPrice(product);
      }, 0);
      return productTotal;
    });

    const totalAmountPayable = computed(() =>{
      return amountPayable.value + deliveryCharge.value;
    })

    return{
      deliveryCharge,
      amountPayable,
      totalAmountPayable,
    }

  }

}
</script>

<style lang="scss" scoped>
$thisRed: #f7444e;
.head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;

    label{
    font-size: 18px;
    font-weight: bold;
  }
  }
  .body{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
    font-size: 13px;
  }
  .foot{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 16px;
    font-size: 13px;

    div{
      display: flex;
      .NT{
        font-size: 13px;
        font-weight: bold;
        color: $thisRed;
        margin-top: 5px;
        margin-right: 5px;
      }
      .dollar{
        font-size: 22px;
        font-weight: bold;
        color: $thisRed;
      }
    }    
  }
</style>