<template>
  <div class="paddingLeftRight16">
    <div class=" InformationCheckDiv">
      <div>
        <input 
        @change="emitPrivacyChecked" 
        v-model="isPrivacyChecked" 
        class="checkbox" 
        type="checkbox">
      </div>
      <div>
        <p>『我同意以上述資料註冊會員，並同意商店服務條款/會員責任規範及個資聲明。』</p>
        <p class="pointRed"  v-if="showPrivacyWarning">請勾選同意個資聲明以繼續結帳</p>
      </div>
      
    </div>
    <div class=" InformationCheckDiv">
      <div>
        <input 
        @change="emitShippingChecked" 
        v-model="isShippingChecked"
        class="checkbox" 
        type="checkbox">
      </div>
      <div>
        <p>為保障彼此之權益，賣家在收到您的訂單後仍保有決定是否接受訂單及出貨與否之權利</p>
        <p class="pointRed"  v-if="showShippingWarning">請勾選同意以繼續結帳</p>
      </div>      
    </div>
    <div class=" InformationCheckDiv">
      <div>
        <input @change="emitMarketingChecked" class="checkbox" type="checkbox">
      </div>
      <p>『我同意接收商家發送的電子報及行銷訊息』</p>
    </div>          
  </div>
  
</template>

<script>
import { computed, inject,  onMounted,  onUnmounted,  ref } from 'vue';

export default {
  setup(){
    const eventBus = inject('eventBus')
    onMounted(()=>{
      eventBus.on('showPrivacyWarning', ()=>{
        hasInteractedWithPrivacy.value = true;
      });
      eventBus.on('showShippingWarning', ()=>{
        hasInteractedWithShipping.value = true;
      })
    })
    onUnmounted(()=>{
      eventBus.off('showShippingWarning', ()=>{
        hasInteractedWithShipping.value = true;
      })
    })

    //勾選警告
    const hasInteractedWithPrivacy = ref(false);
    const isPrivacyChecked = ref(false);
    const hasInteractedWithShipping = ref(false);
    const isShippingChecked = ref(false);

    const showPrivacyWarning = computed(() => hasInteractedWithPrivacy.value && !isPrivacyChecked.value);
    const showShippingWarning = computed(() => hasInteractedWithShipping.value && !isShippingChecked.value);

    const emitPrivacyChecked = (event) =>{
      hasInteractedWithPrivacy.value = true;
      eventBus.emit('emitPrivacyChecked', event.target.checked);
    };
    const emitShippingChecked = (event) =>{
      hasInteractedWithShipping.value = true;
      eventBus.emit('emitShippingChecked', event.target.checked);
    };
    const emitMarketingChecked = (event) =>{
      eventBus.emit('emitMarketingChecked', event.target.checked);
    };

    return{
      isPrivacyChecked,
      isShippingChecked,
      showPrivacyWarning,
      showShippingWarning,
      emitPrivacyChecked,
      emitShippingChecked,
      emitMarketingChecked,
    }
  }

}
</script>

<style lang="scss" scoped>
.pointRed{
  display: block !important;
  color: #dc3545 !important; 
}
</style>