<script>
import { inject, nextTick, ref } from 'vue';
import Cpy from './Cpy.vue';
export default {
  components:{
    Cpy
  },
  setup(){
    const eventBus = inject('eventBus');

    const scrollToWhySection = () => {
        eventBus.emit('CallHomeToScrollSection','.why-section'); 
    };
    const scrollToProduuctSection = () => {
      eventBus.emit('CallHomeToScrollSection','.product-section')
    };
    const scrollToContactSection = () => {
      eventBus.emit('CallHomeToScrollSection','.contact-section')
    }
    const scrollToSubscribeSection = () => {
      eventBus.emit('CallHomeToScrollSection','.subdcibe-section')
    }

    const emailInput = ref(null);
    const submit = ()=>{
      nextTick(()=>{
        //顯示輸入成功並清空input
        eventBus.emit('ShowMessageDisplay',true);
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', '感謝您的訂閱！最新消息將會寄送至您的信箱！');
          eventBus.emit('ShowMessageButton')
        })        
        console.log(emailInput.value.value);
        emailInput.value.value = '';        
      })
    }
    return{
      scrollToWhySection,
      scrollToProduuctSection,
      scrollToContactSection,
      scrollToSubscribeSection,
      emailInput,
      submit
    }
  }
}
