<template>
  <section class="subscribe_section">
  <div class="container-fuild">
    <div class="box">
        <div class="row">
          <div class="col-md-6 offset-md-3">
              <div class="subscribe_form ">
                <div class="heading_container heading_center">
                    <h3>訂閱以取得折扣優惠</h3>
                </div>
                <p>立即訂閱，搶先取得專屬折扣與最新優惠資訊！不容錯過的限時折扣，隨時掌握購物好康！</p>
                <form @submit.prevent="submit" action="">
                    <input 
                    ref="emailInput"
                    autocomplete="off"
                    autocapitalize="off"
                    type="email" 
                    placeholder="輸入信箱"
                    required>
                    <button type="submit" @click="subscribeIt">
                    訂閱
                    </button>
                </form>
              </div>
          </div>
        </div>
    </div>
  </div>
</section>
</template>

<script>
import { inject, nextTick, ref } from 'vue';

export default {
  setup(){
    const emailInput = ref(null);
    const eventBus = inject('eventBus');

    const submit = ()=>{
      nextTick(()=>{
        //顯示輸入成功並清空input
        eventBus.emit('ShowMessageDisplay',true);
        nextTick(()=>{
          eventBus.emit('sentCheckMessage', '感謝您的訂閱！折扣碼與優惠資訊將會寄送至您的信箱');
          eventBus.emit('ShowMessageButton')
        })        
        console.log(emailInput.value.value);
        emailInput.value.value = '';        
      })
      }

    return{
      submit,
      emailInput,
      
    }
  }

}
</script>

<style scoped>
h3{
  font-family: Montserrat, sans-serif;
}

</style>