<template>
  <div class="allDiv">
    <div class="info">
      <div class="imageInfo">
        <img :src="product.imageURL" alt="Product Image">
      </div>
      <div class="DetailInfo">
        <div class="priceInfo">
          <h1>{{ product.name }}</h1>
          <p class="productIntroduce">{{ product.introduce }}</p>
          <p>建議售價</p>
          <h1 class="price">NT${{ product.dollar }}</h1>
        </div>
        
        <div class="quantityInfo">
          <div>
            <button class="quantityButton" @click="minusInput">-</button>
            <input type="text" v-model="quantity " placeholder="1">
            <button class="quantityButton" @click="plusInput">+</button>
          </div>
          <div>
            <button class="checkButton" @click="buyIt">加入購物車</button>
          </div>
        </div>      
      </div>   
    </div>
  </div>
  

  <div v-if="checkMarkVisible" class="checkMarkAnimation">
    <div class="checkMarkDiv">
      <div>
        <svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
          <circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none"/> 
          <path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
        </svg>
      </div>  
      <p class="checkMarkFont">已加入購物車</p>    
    </div>
    <div class="checkMarkDivBackground"></div>
  </div>
</template>

<script>
import { inject, onMounted, reactive, ref } from 'vue';
import { useRouter } from 'vue-router';

export default {
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  setup(props){
    onMounted(() => {
      loadFromStorage();
      window.scroll(0,0);
    });
    const eventBus = inject('eventBus');
    const router = useRouter();

    const quantity = ref(1);
    const ProductDetails = reactive([]);
    const checkMarkVisible = ref(false);

    const loadFromStorage = () =>{
      const storedProduct = JSON.parse(localStorage.getItem('ProductDetails'));
      if(storedProduct){
        ProductDetails.splice(0,ProductDetails.length, ...storedProduct);
      }
    };
    const saveThisProductToStorage = () => {
      localStorage.setItem('ProductDetails',JSON.stringify(ProductDetails));
    };
    const plusInput= () => {
      quantity.value += 1;
    }
    const minusInput = () => {
      if(quantity.value >1) {
        quantity.value -= 1;
      }
    }
    const buyIt = () => {      
      const existingProduct = ProductDetails.find(p => p.thisProductName === props.product.name);
      if(existingProduct){
        existingProduct.thisProductAllQuantity += quantity.value;
        existingProduct.thisPproductAllPrice += (quantity.value)*(props.product.dollar);
      } else {
        ProductDetails.push({
          thisProductName: props.product.name,
          thisProductOnePrice: props.product.dollar,
          thisProductImageUrl: props.product.imageURL,
          thisProductAllQuantity: quantity.value,
        });
      }
      saveThisProductToStorage();
      eventBus.emit('updateProductDetails', ProductDetails);
      //讓checkMarkAnimation出現3秒後消失
      checkMarkVisible.value = true;
      setTimeout(() => {
        checkMarkVisible.value = false;
        router.go(-1);
      },2000);
    }
    return {
      quantity,
      ProductDetails,
      plusInput,
      minusInput,
      buyIt,
      checkMarkVisible
    }
  }
}
</script>

<style lang="scss" scoped>
*{
  padding:0;
  margin:0
}
.allDiv{
  padding: 20px;
  box-sizing: border-box;
}
.info{  
  display: flex;
  justify-content: center;
  flex-direction: row;
  gap: 32px;

  .imageInfo{
    display: flex;
    width: 450px;
    height: 450px;
    justify-content: center;
    align-items: center;    
    background: #eeeeee; 
    margin: 10px;
    box-sizing: border-box;
    overflow: hidden;

    img{
      height: 450px;
      object-fit: cover;
      padding: 20px;
    }
  }
  .DetailInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 10px;

    h1{
      font-size: 24px;
      font-family: "Playfair Display", serif;
    }
    p{
      font-size: 14px;
      padding: 5px 0;
    }
    .productIntroduce{
      width: 545px;
      color: rgb(133, 133, 133);
      padding: 8px 0;
    }
    .price{
      font-family:Montserrat, sans-serif;
      font-weight: bold;
      padding: 0;
      color: rgb(229, 85, 85);
    }
  }

  .quantityInfo{
    display: flex;
    flex-direction: column;
    gap: 8px;

    div{
      display: flex;
    }
    .quantityButton{
      height: 40px;
      width: 42px;
      background-color: rgb(255, 255, 255);
      border: 1px solid rgb(226, 226, 226);
      font-size: 40px;
      font-weight: lighter;
      display: flex;
      justify-content: center;
      align-items: center;
      outline: none;
    }
    input{
      height: 40px;
      width: 140px;
      border: 1px solid rgb(226, 226, 226);
      text-align: center;
    }
    .checkButton{
      height: 40px;
      width: 222px;
      font-size: 16px;
      background-color: #fff;
      color: rgb(247, 68, 78);
      border: 1px solid rgb(247, 68, 78);
      border-radius: 4px;
      transition: 0.2s;
    }
    .checkButton:hover{      
      color: #fff;
      background-color: rgb(247, 68, 78);
      
    }
  }
}
@media (max-width:991px) {
  .allDiv{
    display: flex;
    flex-direction: column;
    align-items: center;

    .info{
      display: flex;
      flex-direction: column;
      align-items: center;

      .DetailInfo{
        width: 100%;
      }
    }
  }  
}

.checkmark__circle{
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;stroke-miterlimit: 10;
  stroke: #7ac142;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards
}
.checkmark{
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  /* margin: 10% auto; */
  box-shadow: inset 0px 0px 0px #7ac142;
  animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both
}
.checkmark__check{
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards
}
@keyframes stroke{100%{stroke-dashoffset: 0}}
@keyframes scale{0%, 100%{transform: none}50%{transform: scale3d(1.1, 1.1, 1)}}
@keyframes fill{100%{box-shadow: inset 0px 0px 0px 30px #7ac142}}

.checkMarkAnimation{

  .checkMarkDivBackground{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);
    z-index: 98;
  }
  .checkMarkDiv{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 300px;
    height: 300px;
    border-radius: 12px;
    justify-content: center;
    align-items: center;
    z-index: 99;

      div{
        margin-bottom: 15px;
      }

    .checkMarkFont{
      color: #7ac142;
      font-size: 2rem;
      text-align: center;
    }
  }
}

@media (max-width:670px){
  .allDiv{
    padding: 10px;
    box-sizing: border-box;
  }
  .info{  
    width: 100%;

    .imageInfo{
      display: flex;
      width: auto;
      height: auto;
      }
  }
  .DetailInfo{

    .priceInfo{

      .productIntroduce{
        width: 100%;
      }
    }
  }
  
}


</style>
