import {createRouter, createWebHashHistory} from 'vue-router';
import Home from "@/components/FrontSide/Home.vue";
import ProductPage from '@/components/FrontSide/ProductPage.vue';
import ShoppingCart from '@/components/FrontSide/ShoppingCart.vue';

const routes = [
  {
    path:'/',
    name:'Home',
    component: Home,
    meta:{title:'成衣購物網'}
  },
  {
    path:'/ProductPage',
    name:'ProductPage',
    component: ProductPage,
    props: route => ({ product: JSON.parse(route.query.product) }),
    meta:{title:'成衣購物網'}
  },
  {
    path:'/ShoppingCart',
    name:'ShoppingCart',
    component:ShoppingCart,
    meta:{title:'成衣購物網'}
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.afterEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  }
});

export default router;