<template>
    <div class="paddingLeftRight16 CustomerBill">
      <label>電子發票</label>
      <div class="billDiv marginBottom12px thisbillDiv">
        <div @click="selectBill('personCarrier')" class="billButton">
          <div :class="selectedBill === 'personCarrier' ? 'checkCircleIt' : 'checkCircle'"></div>
          會員載具(個人)
        </div>
        <div @click="selectBill('GUI')" class="billButton">
          <div :class="selectedBill === 'GUI' ? 'checkCircleIt' : 'checkCircle'"></div>
          公司用(統編)
        </div>
        <div @click="selectBill('phoneCarrier')" class="billButton">
          <div :class="selectedBill === 'phoneCarrier' ? 'checkCircleIt' : 'checkCircle'"></div>
          手機載具
        </div>
        <div @click="selectBill('CDC')" class="billButton">
          <div :class="selectedBill === 'CDC' ? 'checkCircleIt' : 'checkCircle'"></div>
          自然人憑證
        </div>
        <div @click="selectBill('SWO')" class="billButton">
          <div :class="selectedBill === 'SWO' ? 'checkCircleIt' : 'checkCircle'"></div>
          捐贈碼
        </div>
      </div>
      <div v-if="selectedBill === 'GUI'">
        <label>統一編號</label>
        <div class="ButtonDiv">
          <div class="GUIInput">
            <input class="border6px" ref="GUIInput" type="text" placeholder="請輸入統一編號">
            <div class="pointRed" ref="errorGUIMsg">請輸入統一編號</div>
          </div>
          <div class="GUIInput">
            <input class="border6px" ref="GUITitleInput" type="text" placeholder="請輸入發票Title">
            <div class="pointRed" ref="errorGUITitleMsg">請輸入發票Title</div>
          </div>                  
        </div>              
      </div>
      <div v-if="selectedBill === 'phoneCarrier'">
        <label>手機條碼</label>
        <div>
          <input class="border6px" ref="phoneCarrierInput" type="text" placeholder="手機條碼 第一碼務必為 /，後面為7碼(大寫英文，符號，數字)">
          <div class="pointRed" ref="errorphoneCarrierMsg">請輸入手機條碼</div>
        </div>              
      </div>
      <div v-if="selectedBill === 'CDC'">
        <label>自然人憑證</label>
        <div>
          <input type="text" ref="CDCInput" class="border6px" placeholder="請輸入您的自然人憑證號碼，格式需為2碼大寫英文及14碼數字">
          <div class="pointRed" ref="errorCDCMsg">請輸入您的自然人憑證號碼，格式需為2碼大寫英文及14碼數字</div>
        </div>              
      </div>
      <div v-if="selectedBill === 'SWO'">
        <label>捐贈碼</label>
        <div>
          <input type="text" ref="SWOInput" class="border6px" placeholder="請輸入欲捐贈單位的捐贈碼">
          <div class="pointRed" ref="errorSWOMsg">請輸入欲捐贈單位的捐贈碼</div>
        </div>
        <div class="pointGreen">(備註：若捐贈碼填錯，將會把發票轉贈「兒褔聯盟」)</div>
      </div>
      <div @click="selectBillRull" class="AllBillLow">
        <div class="billLow">
          <p>依統一發票使用辦法規定</p>              
          <svg v-if="!selectedBillRull" width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 3L6 9L11 3" stroke="#8E99AD" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path></svg>
          <svg v-if="selectedBillRull" width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M11 9L6 3L1 9" stroke="#8E99AD" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path></svg>
        </div>
        <transition name="slide-fade">
          <div v-if="selectedBillRull" class="billLowdown">
            <p>依統一發票使用辦法規定：個人發票一經開立，無法更改或改開公司戶發票，需開立統編請選擇『公司用(統編) 』，請務必確認選用之電子發票載具類型是否正確，一經開立不得要求更改。</p>
            <br>
            <p>選用『會員載具(個人)』將開立「雲端發票」，開立後會提供由順立智慧股份有限公司代為開立之發票明細(內含發票號碼)，將不會提供發票紙本或電子檔案，請務必如實填寫購買人資料，若發票中獎將以當初購買人留存之連絡資訊發送通知，購買人收到通知後，可自行使用7-11的ibon機台操作列印中獎之發票，該紙本即可進行兌獎。如原資料錯誤或其他原因致使無法通知原購買人時，本公司不再另行通知亦無法協助後續中獎發票處理。</p>
            <br>
            <p>因網路金流收款是委由 CYBERBIZ 集團（順立智慧股份有限公司及子公司順達智慧股份有限公司）維運， 故電子發票抬頭為「順立智慧股份有限公司」或「順達智慧股份有限公司」。 關於商品之退/換貨、保固、維修與其他商品相關問題，敬請洽詢 光光盒餐GuangGuang 辦理。</p>
          </div>
        </transition>
      </div>              
    </div>
</template>

<script>
import { inject, nextTick, onMounted, onUnmounted, ref, watch } from 'vue';
export default {
  setup(){
    const eventBus = inject('eventBus');

    //發票方式
    const selectedBill = ref('personCarrier');
    const selectBill = (method) =>{
      selectedBill.value = method;
    }
    
    //統一發票規定
    const selectedBillRull = ref(false);
    const selectBillRull = () =>{
      selectedBillRull.value = !selectedBillRull.value;
    }

    //公司統編警告
    const GUIInput = ref(null);
    const errorGUIMsg = ref(null);
    const GUITitleInput = ref(null);
    const errorGUITitleMsg = ref(null);

    const checkBillGUI = () => {
      if (GUIInput.value) {
        GUIInput.value.addEventListener('blur', function () {
          const GUIValue = GUIInput.value.value.trim();

          if (GUIValue === '') {
            errorGUIMsg.value.style.display = 'block';
            GUIInput.value.classList.add('error');
            return false;
          } else {
            errorGUIMsg.value.style.display = 'none';
            GUIInput.value.classList.remove('error');
          }
        });
      }
      if (GUITitleInput.value) {
        GUITitleInput.value.addEventListener('blur', function () {
          const GUITitleValue = GUITitleInput.value.value.trim();

          if (GUITitleValue === '') {
            errorGUITitleMsg.value.style.display = 'block';
            GUITitleInput.value.classList.add('error');
            return false;
          } else {
            errorGUITitleMsg.value.style.display = 'none';
            GUITitleInput.value.classList.remove('error');
          }
        });
      }
    };

    //手機條碼警告
    const phoneCarrierInput = ref(null);
    const errorphoneCarrierMsg = ref(null);

    const checkBillPhoneCarrier = () => {
      if (phoneCarrierInput.value) {
        phoneCarrierInput.value.addEventListener('blur', function () {
          const phoneCarrierValue = phoneCarrierInput.value.value.trim();

          if (phoneCarrierValue === '') {
            errorphoneCarrierMsg.value.style.display = 'block';
            phoneCarrierInput.value.classList.add('error');
          } else {
            errorphoneCarrierMsg.value.style.display = 'none';
            phoneCarrierInput.value.classList.remove('error');
          }
        });
      }
    }

    //自然人憑證警告
    const CDCInput = ref(null);
    const errorCDCMsg = ref(null);

    const checkBillCDC = () => {
      if (CDCInput.value) {
        // 限制輸入行為
        CDCInput.value.addEventListener('input', function () {
          let CDCValue = CDCInput.value.value.trim();

          CDCValue = CDCValue.slice(0, 2).toUpperCase() + CDCValue.slice(2);
          CDCValue = CDCValue.replace(/[^A-Z]/g, '').slice(0, 2) + CDCValue.slice(2).replace(/\D/g, '').slice(0, 14);
          if (CDCValue.length > 16) {
            CDCValue = CDCValue.slice(0, 16);
          }
          CDCInput.value.value = CDCValue;
        });

        // 檢查格式是否符合要求
        CDCInput.value.addEventListener('blur', function () {
          const CDCValue = CDCInput.value.value.trim();
          const CDCFormat = /^[A-Z]{2}\d{14}$/;

          if (CDCValue === '' || !CDCFormat.test(CDCValue)) {
            errorCDCMsg.value.style.display = 'block';
            CDCInput.value.classList.add('error');
            return false;
          } else {
            errorCDCMsg.value.style.display = 'none';
            CDCInput.value.classList.remove('error');
          }
        });
      }
    }

    //捐贈碼警告
    const SWOInput = ref(null);
    const errorSWOMsg = ref(null);

    const checkBillSWO = () =>{
      if (SWOInput.value) {
        SWOInput.value.addEventListener('blur', function () {
          const SWOValue = SWOInput.value.value.trim();

          if (SWOValue === '') {
            errorSWOMsg.value.style.display = 'block';
            SWOInput.value.classList.add('error');
            return false;
          } else {
            errorSWOMsg.value.style.display = 'none';
            SWOInput.value.classList.remove('error');
          }
        });
      }    
    };

    watch(selectedBill, async(newVal) => {
      if(newVal === 'GUI'){
        await nextTick();
        checkBillGUI();
      } else if(newVal === 'phoneCarrier'){
        await nextTick();
        checkBillPhoneCarrier();
      } else if(newVal === 'CDC'){
        await nextTick();
        checkBillCDC();
      } else if(newVal === 'SWO'){
        await nextTick();
        checkBillSWO();
      }
    });

    const validateAllInputs = ()=>{
      if (GUIInput.value){
        GUIInput.value.dispatchEvent(new Event('blur'));
      }
      if (GUITitleInput.value) {
        GUITitleInput.value.dispatchEvent(new Event('blur'));
      }
      if (phoneCarrierInput.value){
        phoneCarrierInput.value.dispatchEvent(new Event('blur'));
      }
      if (CDCInput.value){
        CDCInput.value.dispatchEvent(new Event('blur'));
      }
      if (SWOInput.value){
        SWOInput.value.dispatchEvent(new Event('blur'));
      }
    }

    const scrollTothere =(selector)=>{
      const element = document.querySelector(selector);
      if(element){
        element.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
        });
      }
    }

    onMounted(()=>{
      eventBus.on('sentToAskBill',()=>{
        const isBillValidate = validateAllInputs();
        eventBus.emit('sentBackAskBill',isBillValidate);
      });
      eventBus.on('ScrollWindow',scrollTothere);
    })

    onUnmounted(()=>{
      eventBus.off('sentToAskBill')
      eventBus.off('ScrollWindow',scrollTothere)
    })

    return{
      selectedBill,
      selectBill,
      selectedBillRull,
      selectBillRull,
      GUIInput,
      errorGUIMsg,
      GUITitleInput,
      errorGUITitleMsg,
      phoneCarrierInput,
      errorphoneCarrierMsg,
      CDCInput,
      errorCDCMsg,
      SWOInput,
      errorSWOMsg

    }
  }
}
</script>

<style lang="scss" scoped>
</style>