<script>
import { computed, reactive } from 'vue';
export default {
  setup() {
    const customer = reactive([
      {
        name: '林小美',
        imageURL: `images/person2.jpg`,
        say: '這是我用過最好的產品！效果比預期還要好，客服也很親切，值得推薦！'
      },
      {
        name: '王大明',
        imageURL: `images/person1.jpg`,
        say: '服務速度很快，產品質量也很不錯，這是一次非常愉快的購物經驗！'
      },
      {
        name: '張麗華',
        imageURL: `images/person3.jpg`,
        say: '朋友推薦來的，果然沒有讓我失望。包裝也很精美，收到時完好無損。'
      },
      {
        name: '陳志強',
        imageURL: `images/person4.jpg`,
        say: '價錢合理，品質也有保證。網站很容易操作，整體非常方便又省時。'
      },
      {
