<template>
  <div class="paddingLeftRight16">
    <label>付款方式</label>
    <div class="ButtonDiv">
      <div class="chooseButtonCheck">
        <div class="checkCircleIt"></div>
        信用卡
      </div>
    </div>
  </div>
  <div class="paddingLeftRight16 marginBottom12px">
    <label>卡號</label>
    <div class="cardinputDiv">
      <div class="cardType">
        <img src="images/credit-card.svg" alt="">
      </div>
      <input 
      type="text" 
      class="border6px"
      v-model="formattedNumber"
      @input="formatCardNumber"
      placeholder="**** **** **** ****">
    </div>
  </div>
  <div class="paddingLeftRight16">
    <div class="marginBottom12px">
      <label for="">卡片有效年月</label>
      <input 
      type="text"
      class="border6px"
      v-model="formattedDate" 
      @input="formatCardExpiryDate" 
      maxlength="5"
      placeholder="MM / YY">
    </div>
    <div class="marginBottom12px">
      <label for="">CVV / CVC</label>
      <input 
      type="text" 
      class="border6px"
      v-model="cvv"
      @input="formatCVV"
      placeholder="卡片背面檢查碼">
    </div>
  </div>
  <div class="paddingLeftRight16 marginBottom12px">
    <label>持卡人姓名</label>
    <input 
    type="text"
    class="border6px">
  </div>
  <div class="paddingLeftRight16">
    <label>備註</label>
    <textarea class="border6px"></textarea>
  </div>
</template>

<script>
import { ref} from 'vue';
export default {
  setup(){
    //卡片有效年月
    const formattedDate = ref('');

    const formatCardExpiryDate = (event) => {
      let input = event.target.value;

      input = input.replace(/\D/g, '');

      if (input.length >= 1) {
        if (input[0] > '1') {
          input = '0' + input;
        }
        if(input[0] === '1' && input.length > 1){
          if(input[1] > '2'){
            input = input[0] + '2';
          }
        }
      }
      if (input.length >= 2) {
        input = input.slice(0, 2) + '/' + input.slice(2);
      }
      //限制只有31日
      if (['01', '03', '05', '07', '08', '10', '12'].includes(input.slice(0, 2))) {
        if (input.length > 3 && input[3] > '3') {
          input = input.substring(0, 3) + '3' + input.substring(4);
        }
        if (input.length > 4 && input[3] === '3' && input[4] > '1') {
          input = input.substring(0, 4) + '1';
        }
      }
      // 處理 30 天的月份 (4, 6, 9, 11)
      if (['04', '06', '09', '11'].includes(input.slice(0, 2))) {
        if (input.length > 3 && input[3] > '3') {
          input = input.substring(0, 3) + '3' + input.substring(4);
        }
        if (input.length > 4 && input[3] === '3' && input[4] > '0') {
          input = input.substring(0, 4) + '0';
        }
      }
      // 處理 2 月的日期限制（最多 29 天）
      if (input.slice(0, 2) === '02') {
        if (input.length > 3 && input[3] > '2') {
          input = input.substring(0, 3) + '2' + input.substring(4);
        }
        if (input.length > 4 && input[3] === '2' && input[4] > '9') {
          input = input.substring(0, 4) + '9';
        }
      }

      if (event.inputType === 'deleteContentBackward' && input.length === 3) {
        input = input.slice(0, 2);
      }
      if (input.length > 5) {
        input = input.slice(0, 5);
      }
      formattedDate.value = input;
    };

    //信用卡號限制
    const formattedNumber = ref('');
    const formatCardNumber = (event) =>{
      let input = event.target.value;
      input = input.replace(/\D/g, '');
      if(input.length > 16){
        input = input.slice(0,16);
      }
      formattedNumber.value = input;
    }

    //信用卡檢查碼限制
    const cvv =ref("");
    const formatCVV = (event) =>{
      let input = event.target.value;
      input = input.replace(/\D/g, '');
      if(input.length > 4){
        input = input.slice(0,4);
      }
      cvv.value = input
    }
    return{
      formattedDate,
      formatCardExpiryDate,
      formattedNumber,
      formatCardNumber,
      cvv,
      formatCVV,
    }
  }
}
</script>

<style lang="scss" scoped>
</style>