<template>
  <div class="paddingLeftRight16">
    <label>配送方式</label>
    <div class="ButtonDiv">
      <div 
      @click="selectDelivery('home')" 
      :class="selectedDelivery === 'home' ? 'chooseButtonCheck' : 'chooseButton'">
        <div :class="selectedDelivery === 'home' ? 'checkCircleIt' : 'checkCircle'"></div>
        宅配
      </div>
      <div 
      @click="selectDelivery('store')" 
      :class="selectedDelivery === 'store' ? 'chooseButtonCheck' : 'chooseButton'">
        <div :class="selectedDelivery === 'store' ? 'checkCircleIt' : 'checkCircle'"></div>
        門店自取
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
export default {
  setup(){
    //配送方式
    const selectedDelivery = ref('home');
    const selectDelivery = (method) =>{
      selectedDelivery.value = method;
    }

    return{
      selectedDelivery,
      selectDelivery,
    }
  }

}
</script>

