                      </svg>
                      <p class="ShoppingQquantity">{{ TotalProductQuantity }}</p>
                    </a>
                    
                </router-link>                
                <form class="form-inline">
                    <button class="btn  my-2 my-sm-0 nav_search-btn" type="submit">
                    <i class="fa fa-search" aria-hidden="true"></i>
                    </button>
                </form>
              </ul>
          </div>
        </nav>
    </div>
  </header>
</template>

<script>
import { computed, inject, onMounted, reactive } from 'vue';

export default {
  setup() {
    onMounted(()=>{
      loadFromStorage()
      eventBus.on('updateProductDetails', (updatedProductDetails) => {
        ProductDetails.splice(0, ProductDetails.length, ...updatedProductDetails); // 更新數據
      });
    })
    
    const eventBus = inject('eventBus'); // 获取全局的 eventBus
    const ProductDetails = reactive([]);

     const loadFromStorage = () =>{
      const storedProduct = JSON.parse(localStorage.getItem('ProductDetails'));
      if(storedProduct){
        ProductDetails.splice(0, ProductDetails.length, ...storedProduct);
      }
    };

    const TotalProductQuantity = computed(() => {
      return ProductDetails.reduce((total, product) => {
        return total + product.thisProductAllQuantity;
      }, 0);
    });

    eventBus.on('updateProductDetails', (updatedProductDetails) => {
      ProductDetails.splice(0, ProductDetails.length, ...updatedProductDetails);
    });

    const scrollToWhySection = () => {
        eventBus.emit('CallHomeToScrollSection','.why-section'); 
    };
    const scrollToProduuctSection = () => {
      eventBus.emit('CallHomeToScrollSection','.product-section')
    };
    const scrollToContactSection = () => {
      eventBus.emit('CallHomeToScrollSection','.contact-section')
    }
    const scrollToSubscribeSection = () => {
      eventBus.emit('CallHomeToScrollSection','.subdcibe-section')
    }

    const routerToShoppingCart = computed(() => {
      return TotalProductQuantity.value > 0 ? '/ShoppingCart' : undefined;
    });

    //購物車關閉header設定
    const dynamicAttributes  = reactive({});

    const updateAttributes  = ()=>{
      const isSmallScreen = window.matchMedia('(max-width: 991px)').matches;
      if (isSmallScreen) {
        dynamicAttributes.type = 'button';
        dynamicAttributes['data-toggle'] = 'collapse';
        dynamicAttributes['data-target'] = '#navbarSupportedContent';
        dynamicAttributes['aria-controls'] = 'navbarSupportedContent';
        dynamicAttributes['aria-expanded'] = 'false';
        dynamicAttributes['aria-label'] = 'Toggle navigation';
      } else {
        // 在大於 991px 的螢幕上移除這些屬性
        Object.keys(dynamicAttributes).forEach((key) => delete dynamicAttributes[key]);
      }
    };

    onMounted(()=>{
      updateAttributes(); // 頁面載入時檢查
      window.addEventListener('resize', updateAttributes); // 監聽螢幕大小變化
    })


    return {
      scrollToWhySection,
      scrollToProduuctSection,
      scrollToContactSection,
      scrollToSubscribeSection,
      TotalProductQuantity,
      routerToShoppingCart,
      dynamicAttributes,
    };
  },
};

</script>

