.shopCartIcon{
  position: relative;
  margin-right: 8px;
}
.ShoppingQquantity{
  position: absolute;
  width: 24px;
  height: 24px;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  line-height: 24px;
  top: 6px;
  left: 40px;
  background-color: #F7444E;
  color: #FFF;
  border-radius: 50px;
  margin: 0;
}
@media (max-width:991px) {
  .ShoppingQquantity{
    left: 44px;
  }
}


</style>