<template>
  <div class="BK">
    <div class="messageDiv">
      <div>{{ message }}</div>
        <br v-if="showButton">
        <div v-if="showButton">
          <button @click="closeButton">確認</button>
        </div>     
    </div>
  </div>
  
</template>

<script>
import { inject, onMounted, onUnmounted, ref } from 'vue';

export default {
  setup(){
    const eventBus = inject('eventBus')
    const message = ref('')
    const showButton = ref(false);

    onMounted(()=>{
      eventBus.on('sentCheckMessage',(msg)=>{
        message.value = msg;
      });
      eventBus.on('ShowMessageButton', ()=>{
        showButton.value = true;
      })
    })
    onUnmounted(()=>{
      eventBus.off('sentCheckMessage');
      eventBus.off('ShowMessageButton');
    })

    const closeButton = ()=>{
      eventBus.emit('ShowMessageDisplay',false)
    }

    return{
      message,
      showButton,
      closeButton,
    }
  } 
}
</script>

<style lang="scss" scoped>
.BK{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.3);
  z-index: 98;
  display: flex;
  align-items: center;
  justify-content: center;

  .messageDiv{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 16px;
    border-radius: 12px;

    div{
      display: flex;
      justify-content: center;

      button {
        width: auto; 
        padding: 8px 16px; 
        border: none;
        background-color: #f7444e;
        color: white;
        border-radius: 4px;
        cursor: pointer;
        text-align: center; 
      }
    }
  }
}



</style>